}
.container .hero .nav .nav-links {
  position: fixed;
  background: black;
  height: 100vh;
  width: 100%;
  z-index: 4;
  top: 0;
  padding-top: 2rem;
  margin-top: -2rem;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  -webkit-clip-path: circle(0px at 91% 10%);
          clip-path: circle(0px at 91% 10%);
  transition: all 1s ease-in-out;
}
.container .hero .nav .nav-links .menu-links {
  font: normal normal normal 52px/60px Libre Caslon Display;
  letter-spacing: 0px;
  color: #cdb071;
  text-transform: uppercase;
  text-decoration: none;
  animation: float-up 2s linear;
  animation-delay: 1.4s;
  animation-iteration-count: 1;
}
@keyframes float-up {
  0% {
    opacity: 0;
    transform: translateY(50px);
  }
  20% {
    opacity: 0.2;
    transform: translateY(40px);
  }
  40% {
    opacity: 0.4;
    transform: translateY(30px);
  }
  60% {
    opacity: 0.6;
    transform: translateY(20px);
  }
  80% {
    opacity: 0.8;
    transform: translateY(10px);
  }
  100% {
    opacity: 1;
    transform: translateY(0px);
  }
}
@media screen and (max-width: 768px) {
  .container .hero .nav .nav-links .menu-links {
    font-size: 2.5rem;
  }
}
